<template lang="pug">
    div
        h3 CATEGORÍA
        p.border-2.border-blue-400.rounded-lg.cursor-pointer(v-show="showall === false" @click="showall = true" class="lg:hidden lg:border-0") {{elegido}}
            i.fal.fa-chevron-down.ml-4
        ul.border-2.border-blue-400.rounded-lg.flex.flex-col(v-show="showall === true" class="lg:border-0")
            li.text-blue-400.order-1(@click="clicked(0)" id="0") Todos (x)
            li.order-2(@click="clicked(1)" id="1") Cat1 (x)
            li.order-2(@click="clicked(2)" id="2") Cat2 (x)
            li.order-2(@click="clicked(3)" id="3") Cat3 (x)
            li.order-2(@click="clicked(4)" id="4") Cat4 (x)
            li.order-2(@click="clicked(5)" id="5") Cat5 (x)
            li.order-2(@click="clicked(6)" id="6") Cat6 (x)
            li.order-2(@click="clicked(7)" id="7") Cat7 (x)
            li.order-2(@click="clicked(8)" id="8") Cat8 (x)
            li.order-2(@click="clicked(9)" id="9") Cat9 (x)

</template>

<script>
    export default {
        name: "DesignCategories",
        data(){
            return {
                anterior: 0,
                elegido: "Todos (x)",
                showall: (window.innerWidth >= 1024)
            }
        },
        methods:{
            clicked(idClickeado){
                let elementoAnterior = document.getElementById(this.anterior);
                elementoAnterior.classList.remove("text-blue-400");
                elementoAnterior.classList.add("text-black");
                let clickedElement = document.getElementById(idClickeado);
                clickedElement.classList.remove("text-black");
                clickedElement.classList.add("text-blue-400");
                this.anterior = idClickeado;
                this.elegido = clickedElement.innerHTML;
                if (window.innerWidth < 1024){
                    elementoAnterior.classList.remove("order-1");
                    elementoAnterior.classList.add("order-2");
                    clickedElement.classList.remove("order-2");
                    clickedElement.classList.add("order-1");
                    this.showall = false;
                }
            }
        }
    }
</script>

<style lang="sass" scoped>
    ul
        li
           cursor: pointer
</style>